.custom-navbar {
  height: 88px;
  background-color: #FFFFFF;
  box-shadow: 0px 0px 6px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 2px;
  width: 100%;
  box-sizing: border-box;
}

.menu-item {
  padding: 0 12px;
  display: inline;
  text-align: center;
  color: #1890FF;
  text-decoration: underline;
  font-size: 1.063em;
  cursor: pointer;
}
